import React, {useState} from 'react';

import {
    Button,
    TextField,
    Dialog, DialogContent, DialogTitle, DialogActions
} from "@material-ui/core";

import {Form} from 'react-bootstrap';

function AddServiceDialog({active, setActive}) {
    const [service, setService] = useState("");

    function onSubmitService(){
        fetch(process.env.REACT_APP_API+'services/?format=json',{
            method: 'POST',
            headers:{
                'Accept':'application/json',
                'Content-Type':'application/json',
                'Authorization': "Token " + localStorage.getItem('id_token')
            },
            body: JSON.stringify({
                "name": service
            }) 
        })
    };

    return (
        <>
            <Dialog open={active} onClose={setActive} maxWidth="xs" fullWidth>
                <DialogTitle>Добавить сервисную компанию</DialogTitle>
                <Form name="addservice" onSubmit={onSubmitService}>
                    <DialogContent>
                        <Form.Group>
                            <Form.Control
                            type="text"
                            id="add_service"
                            onChange={e => setService(e.target.value)} 
                            placeholder="Введите название сервисной компании"
                            />
                        </Form.Group>
                    </DialogContent>
                <DialogActions>
                    <Button type="submit" onClick={setActive} style={{outline: "none"}}>Сохранить</Button>
                </DialogActions>
                </Form>
            </Dialog>   
        </>
    )
}

export default AddServiceDialog;